.container {
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 0 10rem;
    min-width: 2rem;
    border-radius: 15px;
    background-color: var(--primary-color);
    border: 5px solid var(--primary-color);
    margin-bottom: 20px;
}

.container > h2 {
    color: #fff;
    padding-bottom: 20px;
    text-align: center;
}

.socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

.social {
    background-color: var(--third-color);
    padding: 10px;
    margin: 0 5px;
    border-radius: 5px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    outline: 0 solid #afafaf;
    transition: 0.1s;
}

.social > span {
    background-image: url('../../Assets/social-icon.svg');
    background-size: 56px 130px;
    background-repeat: no-repeat;
    height: 28px;
    width: 28px;
}

.social > p {
    color: #fff;
    text-align: center;
    margin-left: 8px;
    font-weight: 500;
}

.social:hover {
    outline: 3px solid #afafaf;
}

.twitter {
    background-position: 0 0;
}

.github {
    background-position: 0 -28px;
}

.roblox {
    background-position: 0 -56px;
}

@media screen and (max-width: 992px) {
    .container {
        margin: 0 8rem;
    }
}

@media screen and (max-width: 480px) {
    .container {
        margin: 0 2rem;
    }
}