@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;800&family=Rubik:wght@500&display=swap');

:root {
  --primary-color: #1b1f24;
  --secondary-color: #17191d;
  --third-color: #101214;
  --link-button: rgb(39, 120, 196);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

.App {
  text-align: center;
  padding: 2rem 0;
  background-color: #22272e;
}