.container {
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 0 10rem;
    min-width: 2rem;
    border-radius: 15px;
    background-color: var(--primary-color);
    border: 5px solid var(--primary-color);
    margin-bottom: 20px;
}

.container > h2 {
    color: #fff;
    padding-bottom: 20px;
    text-align: center;
}

.container > a {
    color: var(--link-button);
    padding-top: 15px;
}

.repos {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.repos > h3 {
    color: #eeeeee;
}

.repo {
    width: 25%;
    height: 14rem;
    margin: 0 0.4rem;
    padding: 8px;
    background-color: var(--third-color);
    border-radius: 10px;
    transition: 0.1s;
    outline: 0 solid #afafaf;
    display: flex;
    flex-direction: column;
}

.repo:hover {
    outline: 3px solid #afafaf;
}

a {
    text-decoration: none;
}

.repo > .img {
    width: 100%;
    background-color: #f5f5f5;
    border-radius: 5px;
    height: 5rem;
    margin-bottom: 8px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.img > img {
    width: 48px;
    height: 48px;
}

.img > .img_info {
    text-align: start;
    color: rgb(37, 37, 37);
}

.img > .img_info > p:last-child {
    font-weight: bold;
}

.info {
    text-align: start;
}

.name {
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    text-overflow: ellipsis;
}

.description {
    padding: 4px 0;
    color: #eeeeee;
    font-size: 14px;
    font-weight: 500;
    text-overflow: ellipsis;
    height: 4rem;
}

.badgeInfo {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.badgeInfo > .badge {
    background-color: var(--primary-color);
    padding: 4px;
    margin: 0 4px;
    border: 2px solid var(--secondary-color);
    border-radius: 5px;
    color: #eeeeee;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.badge > span {
    background-image: url('../../Assets/icon-common-edit.svg');
    background-size: 50px 122px;
    background-repeat: no-repeat;
    height: 18px;
    width: 18px;
    margin-right: 5px;
}

.star {
    background-position: 0 -18px;
}

.fork {
    background-position: 0 -36px;
}

@media screen and (max-width: 992px) {
    .container {
        margin: 0 8rem;
    }
    .repo {
        width: 30%;
        transform: scale(0.8);
    }
}

@media screen and (max-width: 480px) {
    .container {
        margin: 0 2rem;
    }
}