.container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 18px 10px 10px 10px;
    margin: 0 10rem;
    min-width: 2rem;
    border-radius: 15px;
    background-color: var(--primary-color);
    border: 5px solid var(--primary-color);
    margin-bottom: 20px;
}

.container > p {
    color: #fff;
    text-align: start;
    padding-right: 10px;
    font-size: 22px;
}

@media screen and (max-width: 992px) {
    .container {
        margin: 0 8rem;
    }
}

@media screen and (max-width: 480px) {
    .container {
        margin: 0 2rem;
    }
}