.container {
    display: flex;
    flex-direction: column;
    padding: 5px;
    margin: 0 10rem;
    min-width: 2rem;
    border-radius: 15px;
    background-color: var(--primary-color);
    border: 5px solid var(--primary-color);
    margin-bottom: 20px;
}

.upper > img {
    border-radius: 12px;
    height: 14rem;
    width: 100%;
    object-fit: cover;
}

.bottom {
    background: var(--primary-color);
    height: 100%;
    width: 100%;
}

.avatar {
    padding-bottom: 4.8rem;
}

.avatar > img {
    position: absolute;
    height: 200px;
    width: 200px;
    border-radius: 50%;
    object-fit: cover;
    border: 6px solid var(--primary-color);
    transform: translate(-50%, -60%);
}

.name {
    color: #fff;
    font-weight: 700;
    font-size: 26px;
}

.description {
    color: #eeeeee;
    font-weight: 500;
    font-size: 18px;
    padding-bottom: 6px;
}

@media screen and (max-width: 992px) {
    .container {
        margin: 0 8rem;
        margin-bottom: 14px;
    }
    .avatar {
        padding-bottom: 4.4rem;
    }
    .avatar > img {
        height: 180px;
        width: 180px;
    }
    .name {
        font-size: 22px;
    }
}

@media screen and (max-width: 480px) {
    .container {
        margin: 0 2rem;
    }
    .avatar {
        padding-bottom: 3.6rem;
    }
    .avatar > img {
        height: 140px;
        width: 140px;
    }
    .name {
        font-size: 18px;
    }
}